#BookTable{
    padding: 15px 0;
}
 #BookTable input, select{
        border: none;
        border-bottom: 1px solid rgba(128, 128, 128, 0.283);
        outline: none;
       margin: 5px;
       font-size: 1.2rem;
       width: 280px;
       color:var(--darkGreen-bg);
        padding:5px 2px;
        background-color: transparent;
 }
 #BookTable label{
    margin-top: 15px;
    font-size: 1.2rem;
 }
 #BookTable input[type="datetime-local"]{
    margin-bottom: 10px;
 }

 #BookTable form{
    margin-top: 20px;
 }

 #BookTable input[type="range"]::-webkit-slider-thumb{
    background-color: var(--darkGreen-bg); 
    padding: 0;

}
#BookTable img{
    width:90%;
    text-align: center;
}
#BookTable button{
    background-color: var(--darkGreen-bg);
    margin-top: 10px;
    width: 290px;
    padding:5px 8px;
    font-size: 1.2rem;
    transition: 1s ease-in-out;
}
#BookTable button:hover{
    background-color: var(--lemon-bg);
    color:var(--darkGreen-bg);
    border:2px solid var(--darkGreen-bg);
    box-shadow:  2px 4px 4px #000;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}

#BookTable h3{
    color:var(--darkGreen-bg);
    font-weight: 700;
}

#BookTable input, select:focus {
    background-color: transparent;
}

#BookTable input, select:visited {
    background-color: transparent;
}



/* -------- responsive part ----------------- */
@media screen and (min-width: 280px) and (max-width: 600px) {
    #BookTable{
        padding: 15px 0;
    }
     #BookTable input, select{
            border: none;
            border-bottom: 1px solid rgba(128, 128, 128, 0.283);
            outline: none;
           margin: 5px;
           font-size: 1.2rem;
           width: 280px;
           color:var(--darkGreen-bg);
            padding:5px 2px;
            background-color: transparent;
     }
     #BookTable label{
        margin-top: 15px;
        font-size: 1.2rem;
     }
     #BookTable input[type="datetime-local"]{
        margin-bottom: 10px;
     }
    
     #BookTable form{
        margin-top: 20px;
     }
    
     #BookTable input[type="range"]::-webkit-slider-thumb{
        background-color: var(--darkGreen-bg); 
        padding: 0;
    
    }
    #BookTable img{
        width:80%;
        text-align: center;
    }
    #BookTable button{
        background-color: var(--darkGreen-bg);
        margin-top: 10px;
        width: 280px;
        padding:5px 8px;
        font-size: 1.2rem;
        transition: 1s ease-in-out;
    }
    #BookTable button:hover{
        background-color: var(--lemon-bg);
        color:var(--darkGreen-bg);
        border:2px solid var(--darkGreen-bg);
        box-shadow:  2px 4px 4px #000;
    }
    
    /* Chrome, Safari, Edge, Opera */
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
    }
    
    #BookTable h3,h2{
        color:var(--darkGreen-bg);
        font-weight: 700;
        font-size: 1.8rem;
        text-align: center;
    }
    
    #BookTable input, select:focus {
        background-color: transparent;
    }
    
    #BookTable input, select:visited {
        background-color: transparent;
    }
    
}